.Exercise__link::after {
  content: '';
  display: inline-block;
  width: 27px;
  height: 25px;
  margin-bottom: -4px;
  margin-left: var(--space-2);
  background-image: url(/platzi-master-frontend-exercises/static/media/arrows.8e852252.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.Exercise__border {
  position: absolute;
  top: 15px;
  right: -15px;
  z-index: -1;
  width: 100%;
  height: 100%;
  border: 1px solid #ffffff;
  transition: transform .3s;
}

.Exercise:hover .Exercise__border {
  transform: scale(.98);
}

