:root {
  /* Colors */
  --black-color: #333;
  --primaryColor: #c84451;
  --actionColor: #db5132;
  --backgroundGray: #f3f3f3;
  --backgroundDarkGray: #d6d6d6;
  --typographyColor: #5d6270;
  --surfaceColor: #ffffff;

  --primaryColorDark: #001023;
  --primaryColorDark-2: #061B3A;

  /* Typography */
  --font-1: 'Times New Roman', Times, serif;
  --font-2: Helvetica, sans-serif;
  --font-3: 'Lato', sans-serif;
  --font-4: 'Open Sans', sans-serif;

  /* Typo bolds */
  --normal: 400;
  --semi-bold: 600;
  --bold: 700;
  --extra-bold: 800;

  /* Spacing */
  --space-half: 5px;
  --space: 10px;
  --space-2: 20px;
  --space-3: 30px;
  --space-4: 40px;
  --space-5: 50px;

  /* Border radius */
  --border-radius: 10px;
}

/* ------------------------------------- Globales */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Times New Roman', Times, serif;
  font-family: var(--font-1);
}

p {
  margin-top: 0;
  margin-bottom: 0;
  line-height: .9;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: .9;
}

h1 {
  font-size: 1.3rem;
}

h3 {
  font-size: 1.2rem;
}

h5 {
  font-size: .7rem;
  margin-bottom: 10px;
  margin-bottom: var(--space);
}

a {
  text-decoration: none;
}

figure {
  margin: 0;
}

img {
  vertical-align: top;
}

figcaption {
  font-weight: 700;
  font-weight: var(--bold);
  margin: 5px 0;
}

.button {
  padding: calc(10px / 2) 20px;
  padding: calc(var(--space) / 2) var(--space-2);
  font-weight: 700;
  font-weight: var(--bold);
  color: #ffffff;
  background-color: #4d4c4f;
}

/* ------------------------------------- Utilities */
.u-wrapper {
  padding: 0 20px;
  padding: 0 var(--space-2);
}

.wrapper {
  max-width: 1000px;
  margin: 0 auto;
}

.float-left {
  float: left;
  margin: 10px 10px 0 0;
  margin: var(--space) var(--space) 0 0;
  padding-bottom: 2px;
  border-bottom: 2px solid #333;
  border-bottom: 2px solid var(--black-color);
}

.d-flex {
  display: flex;
}

.w-100 {
  width: 100%;
}

.mt {
  margin-top: 10px;
  margin-top: var(--space);
}

.mt-2 {
  margin-top: 20px;
  margin-top: var(--space-2);
}

.mr {
  margin-right: 10px;
  margin-right: var(--space);
}

.mr-2 {
  margin-right: 20px;
  margin-right: var(--space-2);
}

.mb {
